import React, { useEffect, useState } from "react"
import CustomLink from "./CustomLink"

import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons"

const collapsedHeight = 55
const expandedHeight = 185
const MenuText = ({ children }) => (
  <div
    style={{
      display: "flex",
      alignItems: "center",
      alignContent: "center",
      margin: 0,
      padding: 0,
    }}
  >
    <h6 style={{ margin: "0px 10px", padding: 0}}>
      {children}
    </h6>
  </div>
)
const Spacer = ()=>(
  <div style={{margin:4}}/>
)
export default function NavMenu(props) {
  const [atBottom, setAtBottom] = useState(false)
  const [atTop, setAtTop] = useState(false)
  const [isExpanded, setIsExpanded] = useState(false)
  const atBottomProp = props.atBottom
  const atTopProp = props.atTop
  const [navbarHeight, setNavbarHeight] = useState(collapsedHeight)
  
  useEffect(() => {
    setAtBottom(atBottomProp)
  }, [atBottomProp])
  useEffect(() => {
    setAtTop(atTopProp)
  }, [atTopProp])
  const handleOnClick = () => {
    setIsExpanded(!isExpanded)
  }
  useEffect(() => {
    const newNavbarHeight = isExpanded ? expandedHeight : collapsedHeight
    setNavbarHeight(newNavbarHeight)
  }, [isExpanded])

  return (
    <Navbar className="richNavbar" variant="dark" expand="md" sticky="top">
      <Navbar.Brand href="/">
        <div
          style={{
            display: "flex",
            alignItems: "center",
            alignContent: "center",
            margin: 0,
            padding: 0,
          }}
        >
          <h4
            className="textTitle"
            style={{
              margin: 0,
              padding: 0,
              maxWidth: "100%",
            }}
          >
            Winton Nathan-Roberts
          </h4>
        </div>
      </Navbar.Brand>
      <Navbar.Toggle
        aria-controls="basic-navbar-nav"
        onClick={handleOnClick}
        style={{ border: 0 }}
      >
        <div
          className={"menu"}
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {isExpanded && <FontAwesomeIcon icon={faTimes} size="2x" />}
          {!isExpanded && <FontAwesomeIcon icon={faBars} size="2x" />}
        </div>
      </Navbar.Toggle>
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="ml-auto" variant="pills">
          <CustomLink
            className={atTop && "active"}
            activeClass={atBottom ? "inactive" : "active"}
            to="aboutme"
            spy={true}
            smooth={true}
            offset={-navbarHeight - 30}
            duration={500}
          >
            <MenuText>About</MenuText>
          </CustomLink>
          <Spacer/>
          <CustomLink
            activeClass={atBottom ? "inactive" : "active"}
            to="portfolio"
            spy={true}
            smooth={true}
            offset={-navbarHeight}
            duration={500}
          >
            <MenuText>Portfolio</MenuText>
          </CustomLink>
          <Spacer/>
          <CustomLink
            activeClass="active"
            to="contact"
            spy={true}
            smooth={true}
            offset={-navbarHeight}
            active={atBottom}
            duration={500}
          >
            <MenuText>Contact</MenuText>
          </CustomLink>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}
